import React, { Component } from "react";
import Grid from "@mui/material/Grid";
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import SendIcon from '@mui/icons-material/Send';
import { Box, Stack } from "@mui/material";
import { Skeleton } from "@mui/material";
import axios from "axios";
import { Alarm, LocationOn, Phone } from "@mui/icons-material";

class AppPageContact extends Component {
    constructor(props) {
        super(props);
        this.state = {
            contactInfo: {}, isLoaded: false
        }
    }

    componentDidMount() {
        this.contactInfoData().then(r => {
            this.setState({ isLoaded: true, contactInfo: r.data });
        });
    }

    contactInfoData = async () => {
        return await axios.get("/api/contactinfo/");
    };

    render() {
        const { isLoaded, contactInfo } = this.state;
        return (
            <Grid container className={"mt-2"} spacing={1}>
                <Grid item xs={12} sm={12} md={8} >
                    <Card >
                        <CardContent>
                            <Grid container>
                                <Grid item xs={12} sm={12} md={6}>
                                    <TextField
                                        label="الاسم"
                                        id="outlined-start-adornment"
                                        sx={{ mt: 1, width: '95%' }}
                                    />

                                </Grid>

                                <Grid item xs={12} sm={12} md={6}>
                                    <TextField
                                        fullWidth
                                        label="البريد الالكتروني"
                                        id="outlined-start-adornment"
                                        sx={{ mt: 1, width: '95%' }}
                                    />

                                </Grid>
                                <Grid item xs={12} sm={12} md={6}>
                                    <TextField
                                        label="الموضوع"
                                        id="outlined-start-adornment"
                                        sx={{ mt: 3, width: '95%' }}
                                    />

                                </Grid>

                                <Grid item xs={12} sm={12} md={6}>
                                    <TextField
                                        fullWidth
                                        label="رقم الهاتف"
                                        id="outlined-start-adornment"
                                        sx={{ mt: 3, width: '95%' }}
                                    />

                                </Grid>
                                <Grid item xs={12} sm={12} md={12}>
                                    <TextField
                                        fullWidth
                                        multiline
                                        rows={5}
                                        label="اكتب رسالتك"
                                        id="outlined-start-adornment"
                                        sx={{ mt: 3, width: '97.5%' }}
                                    />

                                </Grid>
                            </Grid>
                        </CardContent>
                        <CardActions sx={{ 'marginRight': 2 }}>
                            < Button variant="contained" startIcon={< SendIcon />}>
                                <Typography className="ms-2">
                                    ارسال
                                </Typography>
                            </Button>
                        </CardActions>
                    </Card>
                </Grid>
                <Grid item xs={12} sm={12} md={4}>
                    {!isLoaded ? (
                        <Stack direction={'column'} spacing={4} style={{ 'textAlign': 'start' }}>
                            {
                                Array.from(new Array(3)).map((el, i) =>
                                    <Card key={i}>
                                        <Box sx={{ display: 'flex', flexDirection: 'column', marginLeft: 2, marginRight: 2 }}>
                                            <CardContent sx={{ flex: '1 0 auto' }}>
                                                <Grid container>
                                                    <Grid item xs={11} sm={11} md={11}>
                                                        {i == 2 ? <Skeleton variant="text" width="40%"
                                                            style={{ marginBottom: 7 }} /> : ''}
                                                        <Skeleton variant="text" width="80%"
                                                            style={{ marginBottom: 7 }} />
                                                        <Skeleton variant="text" width="80%"
                                                            style={{ marginBottom: 7 }} />
                                                    </Grid>
                                                    <Grid item xs={1} sm={1} md={1}>
                                                        <Skeleton animation="wave" variant="circular" width={50} height={50} />
                                                    </Grid>
                                                </Grid>
                                            </CardContent>
                                        </Box>
                                    </Card>
                                )
                            }
                        </Stack>

                    ) : (
                        <Stack direction={'column'} spacing={4} style={{ 'textAlign': 'start' }}>
                            {contactInfo.phone || contactInfo.email ? (
                                <Card>
                                    <Box sx={{ display: 'flex', flexDirection: 'column', marginLeft: 2, marginRight: 2 }}>
                                        <CardContent sx={{ flex: '1 0 auto' }}>
                                            <Grid container>
                                                <Grid item xs={11} sm={11} md={11}>
                                                    <Typography component="div" variant="h6" color="text.secondary">
                                                        {contactInfo.phone}
                                                    </Typography>
                                                    <Typography component="div" variant="h6" color="text.secondary">
                                                        {contactInfo.email}
                                                    </Typography>
                                                </Grid>
                                                <Grid item xs={1} sm={1} md={1}>
                                                    <Phone sx={{ fontSize: 40 }} color="primary" />
                                                </Grid>
                                            </Grid>
                                        </CardContent>
                                    </Box>
                                </Card>
                            ) : ('')}
                            {contactInfo.address ? (
                                <Card>
                                    <Box sx={{ display: 'flex', flexDirection: 'column', marginLeft: 2, marginRight: 2 }}>
                                        <CardContent sx={{ flex: '1 0 auto' }}>
                                            <Grid container>
                                                <Grid item xs={11} sm={11} md={11}>
                                                    <Typography component="div" variant="h5">
                                                        {contactInfo.address}
                                                    </Typography>
                                                </Grid>
                                                <Grid item xs={1} sm={1} md={1}>
                                                    <LocationOn sx={{ fontSize: 40 }} color="primary" />
                                                </Grid>
                                            </Grid>
                                        </CardContent>
                                    </Box>
                                </Card>
                            ) : ('')}
                            {contactInfo.open_from_time && contactInfo.open_to_time ?
                                (
                                    <Card>
                                        <Box sx={{ display: 'flex', flexDirection: 'column', marginLeft: 2, marginRight: 2 }}>
                                            <CardContent sx={{ flex: '1 0 auto' }}>
                                                <Grid container>
                                                    <Grid item xs={11} sm={11} md={11}>
                                                        <Typography component="div" variant="h5" color="primary" sx={{
                                                            fontWeight: 'bold',
                                                        }}>
                                                            اوقات العمل
                                                        </Typography>
                                                        <Typography variant="subtitle1" color="text.secondary" component="div">
                                                            ٢٤ ساعة عمل / ٧ ايام في الاسبوع
                                                        </Typography>
                                                        <Typography variant="subtitle1" color="text.secondary" component="div">
                                                            وقت المكتب: {contactInfo.open_from_time} - {contactInfo.open_to_time}
                                                        </Typography>
                                                    </Grid>
                                                    <Grid item xs={1} sm={1} md={1}>
                                                        <Alarm sx={{ fontSize: 40 }} color="primary" />
                                                    </Grid>
                                                </Grid>
                                            </CardContent>
                                        </Box>
                                    </Card>
                                ) : ('')}
                        </Stack >
                    )}
                </Grid>
            </Grid>
        );
    }
}

export default AppPageContact;